<template>
    <main class="main">
        <Preloader />
        <div class="container">
            <h2 class="subtitle">
                Жаль, что вы нас покидаете
            </h2>
            <span class="description">
                Почему вы решили отменить подписку?
            </span>
            <label v-for="(reason, id) in reasons"
                   :key="reason"
                   :for="id"
                   class="reason__wrapper"
                   @click="selectedReason = id"
            >
                <input :id="id"
                       type="radio"
                       name="reason"
                       class="reason__checkbox"
                       :value="id"
                />
                <span class="reason">
                    {{reason}}
                </span>
            </label>
        </div>
        <div class="buttons__wrapper container">
            <router-link
                class="stay__button"
                :to="`/management/?token=${token}`"
                @click="$store.dispatch('sendUserEvent', 'YardSubManagementStaySubFirst')"
            >
                <Button>
                    Оставить подписку
                </Button>
            </router-link>
            <router-link
                class="stay__button"
                :class="{ inactive: !+selectedReason }"
                :to="`/unsubscribe/?token=${token}&reasonId=${selectedReason}`"
                @click="$store.dispatch('sendUserEvent', 'YardSubManagementCancelProceed')"
            >
                <Button
                    :active="!!+selectedReason"
                    color="gray"
                >
                    Продолжить
                </Button>
            </router-link>
        </div>
    </main>
</template>

<script>
    import { API } from "../components/Constants.js";
    import { defineAsyncComponent } from "vue";
    import { getUnsubscribeReasons } from "../components/ApiRequests";

    export default {
        name: "UnsubscribeReasonSelect",
        components: {
            Preloader: defineAsyncComponent(() => import("@/components/Preloader.vue")),
            Button: defineAsyncComponent(() => import("@/components/Button.vue")),
        },
        data() {
            return {
                token: API.TOKEN,
                reasons: {},
                selectedReason: null,
            };
        },
        mounted() {
            this.getUnsubscribeReasons()
                .then((reasons) => (this.reasons = reasons));
        },
        methods: {
            async getUnsubscribeReasons() {
                return await getUnsubscribeReasons();
            },
        },
    };
</script>

<style scoped>
@import "../assets/main.css";

.main {
  justify-content: space-between;
}

.subtitle {
  width: 100%;
  margin-top: 58px;
  font-weight: 600;
  font-size: 18px;
  line-height: 20px;
  letter-spacing: -0.02em;
}

.description {
  width: 100%;
  margin: 16px 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #707583;
}

.reason__wrapper {
  display: flex;
  width: 100%;
  flex-direction: row;
  align-items: center;
}

.reason {
  flex-grow: 1;
  margin-left: 24px;
  padding: 16px 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.02em;
  cursor: pointer;
}

.reason__wrapper:not(:last-of-type) .reason {
  border-bottom: 1px solid #B9C6D1
}

.reason__checkbox {
  width: 24px;
  min-width: 24px;
  height: 24px;
  min-height: 24px;
  display: flex;
  position: relative;
  margin: 0;
  border-radius: 50%;
  vertical-align: middle;
  border: 1px solid #B9C6D1;
  appearance: none;
  outline: none;
  z-index: 2;
}

.reason__checkbox:checked::after {
  content: "";
  display: flex;
  position: absolute;
  width: 24px;
  height: 24px;
  inset: -1px 0 0 -1px;
  border-radius: 100%;
  background: #1D86FF url("../assets/images/checkbox.svg") 5px center no-repeat;
}

.buttons__wrapper {
  width: 100%;
  justify-content: center;
  padding-bottom: 42px;
}

.stay__button {
  width: 100%;
  display: flex;
  justify-content: center;
  text-decoration: none;
  margin-bottom: 8px;
}

.inactive {
  pointer-events: none;
}
</style>